
//request
import request from '@/utils/Request'
//
export const login = data => {
  return request({
    method: 'POST',
    url: '/mp/v1_0/authorizations',
    // userthis.user.
    data
  })
}

//
export const cropperImage = data => {
  // idbodycollect
  return request({
    method: 'PATCH',
    url: '/mp/v1_0/user/photo',
    data
  })
}
//
export const userChange = data => {
  return request({
    method: 'PATCH',
    url: '/mp/v1_0/user/profile',
    data
  })
}


//
export const userProfile = () => {
  // userloginJSON
  // const user = window.localStorage.setItem('user'),,JSON.parse,getItemuserset
  const user = JSON.parse(window.localStorage.getItem('user'))
  // console.log(user)
  return request({
    method: 'GET',
    url: '/mp/v1_0/user/profile',
    headers: {
    //   Authorization'Bearernetworktoken',token
    //   tokentoken
      Authorization: `Bearer ${user.token}`
      // Authorization: 'Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJleHAiOjE2MzQzNjk0MDcsInVzZXJfaWQiOjEsInJlZnJlc2giOmZhbHNlLCJ2ZXJpZmllZCI6dHJ1ZX0.e9B2z2CFJrncS64pFmJW22VP_VkGexhLIKrFGUqSLvU'
    //   
    }
  })
}