<template>
  <div class="mine-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="mine-content">
        <el-row>
          <el-col :span="15">
            <el-form :model="user" :rules="rules" ref="user" label-width="100px" class="demo-user">
              <el-form-item label="" prop="number">
                {{ user.id }}
              </el-form-item>
              <el-form-item label="" prop="phone">
                {{ user.mobile }}
              </el-form-item>
              <el-form-item label="" prop="name">
                <el-input v-model="user.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="intro">
                <el-input type="textarea" v-model="user.intro"></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input v-model="user.email"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitUserChange" size="small" :loading="submitUserLoading"></el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="4" :offset="2">
            <div class="right-image">
              <!-- inputtype="file"labelel-avatarlabel,inputidlabelfor -->
              <label for="file">
                <el-avatar shape="square" :size="150" :src="user.photo"></el-avatar>
                <p></p>
                <!-- inputinput -->
                <input type="file" hidden id="file" @change="onFileChange" ref="file">
              </label>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-card>
    <el-dialog
      class="el-dialog-img"
      append-to-body
      title=""
      width="50%"
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      @opened="onCropper"
      @closed="onCropperDestroy"
    >
        <div class="proview-image-wrap">
          <img id="image" :src="previewImage" ref="preview-img">
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="croppedImage" :loading="imgData"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
import { userProfile, cropperImage, userChange} from '@/api/user';
import 'cropperjs/dist/cropper.css';
import Cropper from 'cropperjs';
import globalBus from '@/utils/global-bus'

export default {
  data() {
    return {
      // ruleForm: {
      //   name: '',
      //   desc: '',
      //   email: ''
      // },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
      user: {
        //,
        email: '',
        id: '',
        intro: '',
        mobile: '',
        name: '',
        photo: '',
      },
      dialogVisible: false, //false
      previewImage: '', //src
      cropper: null, //cropper
      imgData: false, //loading
      submitUserLoading: false //loading
    }
  },
  created() {
    this.loadUser()
  },
  methods: {
    loadUser() {
      userProfile().then(res => {
        // console.log(res);
        this.user = res.data.data
      })
    },
    onFileChange() {
      const file = this.$refs.file; //DOM
      // console.log(file.files); 
      // console.log(file.files); 
      // console.log(file.files[0]); 
      const blogData = window.URL.createObjectURL(file.files[0]); //srcwindow.URL.createObjectURLH5API
      this.previewImage = blogData


      // console.log('onFileChange'); dom
      this.$refs.file.value = '';
      // console.log('onFileChange');
      this.dialogVisible = true; //
    },
    // 
    handleClose(done) {
      this.$confirm('')
        .then(_ => {
          this.$message({
            type: 'warning',
            message: ''
          })
          done();
        })
        .catch(_ => {});
    },
    onCropper() {
      //new Cropper
      if(this.cropper) {
        this.cropper.replace(this.previewImage)
        //elsereturn
      } else {
      //onFileChangedialogoppend
      const image = this.$refs['preview-img'] //DOMimgrefimgdialog //imageimage
      // const cropper = new Cropper(image, { datacropper
      this.cropper = new Cropper(image, {
        autoCropArea: 1,
        dragMode: 'none',
        background: false
        // crop(event) {
        //   console.log(event.detail.x);
        //   console.log(event.detail.y);
        //   console.log(event.detail.width);
        //   console.log(event.detail.height);
        //   console.log(event.detail.rotate);
        //   console.log(event.detail.scaleX);
        //   console.log(event.detail.scaleY);
        // },
      });
      }
      // replace1dialog@closedcropper2cropperreplacereplace(url[, hasSameSize])onCropper
    },
    onCropperDestroy() {
      // this.cropper.destroy() //onCropper()croppercropperdata
    },
    croppedImage() {
      this.imgData = true
      // cropper
      this.cropper.getCroppedCanvas().toBlob(blob => {
        // console.log(blob);
        const imgData = new FormData();
        imgData.append('photo', blob); //photo
        //,imgDatablog
        cropperImage(imgData).then(res => {
          // console.log(res);
          //
          this.dialogVisible = false
          //
          // this.user.photo = res.data.data.photo
          // blob
          this.user.photo = window.URL.createObjectURL(blob)
          this.$message({
            type: 'success',
            message: ''
          })
          this.imgData = true;
          globalBus.$emit('updataUser', this.user) //
        })
      });
    },
    submitUserChange() {
      this.submitUserLoading = true;
      const { name, intro, email } = this.user
      userChange({
        // this.userthis.user
        name,
        intro,
        email
      }).then(res => {
        // console.log(res);
        this.$message({
            type: 'success',
            message: ''
        })
        this.submitUserLoading = false;
        globalBus.$emit('updataUser', this.user) //
      })
    }
  }
}
</script>

<style lang="less" scoped>
// .mine-content {
//   display: flex;
//   justify-content: space-around;
// }
// /deep/.mine-container {
//   .el-dialog-img {
//     #img{
//       display: flex;
//       justify-content: center;
//     }
//   }
// }
.proview-image-wrap {
  #image {
      display: block;
      /* This rule is very important, please don't ignore this */
      max-width: 100%;
      height: 200px;
  }
}
.right-image {
  // &+
  label {
    cursor: pointer;
  }
}
</style>